import React, {Component} from "react";
import {connect} from "react-redux";
import {addToCart, setSelectedVariant, setSelectedVariantObject} from "eshop/modules/simfree/actions/app";
import {getSelectedVariant, getSelectedVariantObject} from "eshop/modules/simfree/selectors";
import OraTooltip from "eshop/modules/core/components/ui/NoTriggerTooltip";
import ProductStorageCapacityComponent from "eshop/modules/simfree/components/ProductStorageCapacityComponent";
import ComparatorCheckbox from "eshop/modules/simfree/components/comparator/ComparatorCheckbox";


class ProductDetailsHeaderAndColorComponent extends Component {
    constructor(props) {
        super(props);
        console.log(this.props);
    }

    componentDidMount() {
        this.setSelectedVariant({productId: this.props.deviceData.selectedVariant});

    }

    findProductVariant() {
        for (var i = 0; i < this.props.deviceData.variantList.length; i++) {
            if (this.props.deviceData.variantList[i].productId == this.props.selectedVariant
                || (!this.props.selectedVariant && this.props.deviceData.variantList[i].productId == this.props.deviceData.selectedVariant)) {

                return this.props.deviceData.variantList[i];
            }
        }
    }

    _renderSeparator() {
        return (
            <div
                className="l-col l-col-12  u-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                <div className="o-separator u-padding-top-l"></div>
            </div>
        )
    }

    setSelectedVariant(newSelectedVariant, baseProductId, colorUrl) {
        this.props.setSelectedVariant(newSelectedVariant, baseProductId, colorUrl);
        this.props.setSelectedVariantObject(this.findProductVariant());

    }

    setVariantDueToElements(variant, baseProductId, variantColorUrl) {
        var selectedVariant = this.findProductVariant();
        var newSelectedVariant;
        //get pam of already selected partial variants
        var variants = jQuery.extend({}, selectedVariant.setOf);
        var newSelectedVariant = selectedVariant;
        variants[baseProductId] = variant.productId;

        //find variant that maches setOf==variants
        this.props.deviceData.variantList.map(v => {
            if (JSON.stringify(v.setOf) === JSON.stringify(variants)) {
                newSelectedVariant = v;
            }
        });

        this.setSelectedVariant(newSelectedVariant, baseProductId, newSelectedVariant.colorUrl);
    }

    _renderSetColors() {
        let map = this.props.deviceData.setOf;
        var ret = [];
        let that = this;
        Object.keys(map).forEach(function (deviceCode, index, array) {
                let device = map[deviceCode];
                var selectedVariant = that.findProductVariant().setOf[deviceCode];
                ret.push(<ColorChooser key={"colorFor_" + deviceCode} deviceName={device.name} deviceData={device}
                                       selectedVariant={selectedVariant}
                                       onClickHandler={that.setVariantDueToElements.bind(that)}
                                       variantList={device.variantList}
                                       showSeparator={!that.isLast(index, array)}/>);
            }
        );

        return ret;
    }

    isLast(index, array) {
        return index === array.length - 1;
    }

    render() {
        return (
            <div>
                <div
                    className="l-col l-col-small-12 l-col-medium-8 l-col-12  u-small-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <h1 className="h1 u-spacing">{this.props.deviceData.name}</h1>
                </div>
                <div className="l-col l-col-12">
                    <div
                        className="l-col l-col-4 u-small-hide u-medium-hide">
                        <div className="opl-rating">
                            <span className="g-icon g-icon--star g-icon--s"></span>
                            <span className="g-icon g-icon--star g-icon--s"></span>
                            <span className="g-icon g-icon--star g-icon--s"></span>
                            <span className="g-icon g-icon--star g-icon--s"></span>
                            <span className="g-icon g-icon--star g-icon--s"></span>
                            <div style={{width: this.props.deviceData.percentageRating}} className="opl-rating-bar">
                                <span className="g-icon g-icon--star g-icon--s"></span>
                                <span className="g-icon g-icon--star g-icon--s"></span>
                                <span className="g-icon g-icon--star g-icon--s"></span>
                                <span className="g-icon g-icon--star g-icon--s"></span>
                                <span className="g-icon g-icon--star g-icon--s"></span>
                            </div>
                        </div>
                            <p className="small g-gray3-c u-padding-left-s u-inline-block">({this.props.deviceData.numberOfReviewsCustomers})</p>
                            <p className="u-acc-hide">{this.props.deviceData.numberOfReviewsCustomers} opini</p>
                    </div>
                    <div
                        className="l-col l-col-4 u-padding-top-s u-small-hide u-medium-hide">
                        <ComparatorCheckbox product={this.props.deviceData}/>
                    </div>
                </div>
                <div
                    className="l-col l-col-small-12 l-col-medium-6 l-col-4  u-medium-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <div className="u-display_table  u-hide">
                        <p className="u-display_table-cell u-text-right u-small-padding-right-m">Podziel siÄ™</p>
                        <a href="#"
                           className="u-display_table-cell u-text-right g-icon g-icon--social-facebook-2 g-icon--xs-s u-small-padding-right-m"></a>
                        <a href="#" className="u-display_table-cell u-text-right g-icon g-icon--mail g-icon--xs-s"></a>
                    </div>
                </div>
                <div
                    className="l-col l-col-12  u-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <div className="o-separator u-padding-top-l"></div>
                </div>
                <div
                    className="l-col l-col-small-12 l-col-medium-8 l-col-6  u-small-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    {!this.props.deviceData.isSet &&
                    <ColorChooser deviceData={this.props.deviceData} selectedVariant={this.props.selectedVariant}
                                  onClickHandler={this.setSelectedVariant.bind(this)}
                                  variantList={this.props.deviceData.variantList}
                                  showSeparator={false}/>}
                    {this.props.deviceData.isSet && this._renderSetColors()}
                </div>
                <ProductStorageCapacityComponent/>
                <div
                    className="l-col l-col-12  u-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <div className="o-separator u-padding-top-l"></div>
                </div>
            </div>


        )
    }
}


const ColorChooser = (props) => {
    function getColorNameForSelectedVariant(variantList, selectedVariant) {
        let color = "";
        variantList.map(v => {

            if (v.productId == selectedVariant) {
                color = v.colorDefinition[0].name;
            }
        });
        return color;
    }

    let selectedColorString = getColorNameForSelectedVariant(props.variantList, props.selectedVariant);
    return (
        <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 " id="product-basic-information">
            <div className="l-row u-display_table">
                <div className="l-col l-col-12  u-display_table-row" aria-hidden="true">
                            <span className="u-padding-right-s">Kolor {props.deviceName && props.deviceName}:</span>
                            <span className="u-font-bold"> {selectedColorString}</span>
                </div>
                    <div className="l-col l-col-12  u-display_table-row u-padding-top-m">
                        <div className="opl-color-chooser">
                            <fieldset>
                                <legend className="u-acc-hide">Kolor telefonu</legend>
                                {props.variantList.map(variant => {

                                        var cssStyle = variant && variant.colorDefinition && variant.colorDefinition[0] && variant.colorDefinition[0].cssCode && variant.colorDefinition[0].cssCode;
                                        if (variant.colorDefinition)
                                            return (
                                                <OraTooltip id={"colorVariantTooltip" + variant.productId}
                                                            key={"colorVariantTooltip" + variant.productId}
                                                            tooltipContent={"Kolor: " + variant.colorDefinition[0].name}
                                                            className="u-inline">

                                                    <label
                                                        className="opl-color-chooser__item o-tooltip__trigger o-tooltip--top tooltipstered"
                                                        key={"color-chooser-" + variant.productId}
                                                        onClick={() => props.onClickHandler(variant, props.deviceData.productId, variant.colorUrl)}
                                                        data-tooltip-trigger-event="mouseover">
                                                        <input type="radio" name={"color_" + props.deviceData.productId}
                                                               value={variant.productId}
                                                               checked={variant.productId == props.selectedVariant}
                                                               onChange={() => props.onClickHandler(variant, props.deviceData.productId, variant.colorUrl)}
                                                        />
                                                        <span className="opl-color-chooser__color"
                                                              style={{background: cssStyle}}>
                                    </span>
                                                        <span
                                                            className="opl-color-chooser__label">{variant && variant.colorUrl}
                                     </span>
                                                    </label>
                                                </OraTooltip>
                                            )

                                    }
                                )
                                }
                            </fieldset>
                        </div>
                    </div>
                </div>
                {props.showSeparator && <div className="o-separator u-padding-top-l"></div>}
            </div>

    )
}

const mapStateToProps = state => ({
    selectedVariant: getSelectedVariant(state)
});

const mapDispatchToProps = (dispatch, props) => ({
    setSelectedVariant: (selectedVariantCode, baseProductId, colorCode) => dispatch(setSelectedVariant(selectedVariantCode, colorCode, props.filterState)),
    setSelectedVariantObject: (selectedVariantObject) => dispatch(setSelectedVariantObject(selectedVariantObject))

});


export default connect(mapStateToProps, mapDispatchToProps)(ProductDetailsHeaderAndColorComponent);
